.input-container {
  height: 100%;
  background-color: beige;
}

.input-header {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  align-items: center;
  background-color: #3f51b5;
  padding: 4px 16px;
}

.input-row {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.input-row:nth-of-type(2n) {
  background-color: white;
}
.input-row:nth-of-type(2n + 1) {
  background-color: beige;
}

.input-row > * {
  margin: 8px;
}

.input-row.disabled {
  background-color: rgba(255, 0, 0, 0.2);
}
