@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
h1 {
  font-size: 4.209rem;
}
h2 {
  font-size: 3.157rem;
}
h3 {
  font-size: 2.369rem;
}
h4 {
  font-size: 1.777rem;
}
h5 {
  font-size: 1.333rem;
}
h6 {
  font-size: 1rem;
}

* {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 400px;
  grid-template-rows: 1fr -webkit-min-content;
  grid-template-rows: 1fr min-content;
  grid-template-areas:
    "input table"
    "footer footer";
}

header {
  padding: 12px 24px;
  background-color: #3f51b5;
}

header > h3 {
  margin: 0;
  color: white;
  word-break: break-word;
}

footer {
  grid-area: footer;
  z-index: 2;
  background-color: black;
  color: white;
  padding: 4px 16px;
  text-align: center;
}

footer > p {
  margin: 0;
  font-size: 0.75rem;
}

button,
.button {
  color: white;
  text-align: center;
  padding: 8px 16px;
  border: none;
  cursor: pointer;
}

button > p,
.button > p {
  margin: 0 0 0 16px;
}

.color-indicator {
  display: flex;
  flex-flow: row nowrap;
  background-color: #363636;
  border-radius: 16px;
  padding: 8px 16px;
}

.color-indicator > p {
  color: white;
  margin: 0 0 0 12px;
}

.scroll-to-bot {
  position: fixed;
  bottom: 32px;
  right: 448px;
  padding: 4px;
  -webkit-filter: drop-shadow(0 0 2px black);
          filter: drop-shadow(0 0 2px black);
}

.scroll-to-bot > div {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 1024px) {
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  ::-webkit-scrollbar-thumb {
    background: #888888;
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #555555;
  }
}

@media screen and (max-width: 1023px) {
  .app {
    height: revert;
    display: block;
    padding-bottom: 32px;
  }

  .input-container {
    height: auto;
  }

  .timetable {
    height: auto;
  }

  footer {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
  }

  .scroll-to-bot {
    display: none;
    right: 48px;
  }
}

.input-container {
  height: 100%;
  background-color: beige;
}

.input-header {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  align-items: center;
  background-color: #3f51b5;
  padding: 4px 16px;
}

.input-row {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.input-row:nth-of-type(2n) {
  background-color: white;
}
.input-row:nth-of-type(2n + 1) {
  background-color: beige;
}

.input-row > * {
  margin: 8px;
}

.input-row.disabled {
  background-color: rgba(255, 0, 0, 0.2);
}

.modal {
  position: fixed;
  z-index: 5;
  overflow-y: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: white;
  z-index: 6;
  max-width: 768px;
  width: 90%;
  margin: 32px auto 64px auto;
  padding: 1rem 2rem;
  border-radius: 8px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.copy-text {
  width: 100%;
  resize: none;
  box-sizing: border-box;
}

.timetable {
  height: 100%;
  display: grid;
  grid-template-columns: 32px;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
  background-color: white;
  max-width: 400px;
}

.timetable p {
  margin: 0;
}

.timetable .heading {
  text-align: center;
  font-weight: 500;
  align-self: center;
}

.timetable > * {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 48px repeat(18, 1fr);
}

.timestamp {
  border-top: 1px solid black;
  padding-left: 4px;
  font-size: 0.75rem;
}

.course-block {
  background-color: beige;
  margin: 0;
  text-align: center;
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  align-items: center;
}

.course-block-bg {
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  border-radius: 8px;
  opacity: 0.6;
  width: 100%;
  height: 100%;
}

.course-block > *:not(.course-block-bg) {
  margin: 4px;
  z-index: 1;
  font-weight: 500;
  grid-column: 1 / -1;
  word-break: break-word;
}

.course-block > .course-code {
  text-align: center;
  grid-row: 1 / -1;
  line-height: 1;
}

.course-block > .time {
  font-size: 0.563rem;
}
.course-block > .time.start {
  grid-row: 1 / 2;
  justify-self: flex-start;
}
.course-block > .time.end {
  grid-row: 3 / 4;
  justify-self: flex-end;
}

@media screen and (max-width: 1023px) {
  .timetable {
    border: 2px solid black;
    margin: 0 auto;
    height: 95vh;
    box-sizing: border-box;
  }
}

